  z-index: 2;
  width: 100%;

  grid-area: hd;

  margin: 0 0 50px 0;

  font-size: 50px;
  font-weight: bold;
  white-space: pre-line;

  text-align: center;
  text-transform: uppercase;
  color: white;
  text-shadow: 0 2px 4px rgb(71 97 206 / 36%);
}